'use client'

import { useState, useEffect } from 'react'

interface PaymentModalProps {
  onClose: () => void
  onPaymentSuccess: () => void
}

export default function PaymentModal({ onClose, onPaymentSuccess }: PaymentModalProps) {
  const [step, setStep] = useState<'select' | 'pix' | 'checking' | 'success'>('select')
  const [pixCode, setPixCode] = useState('')
  const [qrCodeUrl, setQrCodeUrl] = useState('')
  const [paymentId, setPaymentId] = useState('')
  const [timeLeft, setTimeLeft] = useState(300) // 5 minutos

  // Timer para expira√ß√£o do PIX
  useEffect(() => {
    if (step === 'pix' && timeLeft > 0) {
      const timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000)
      return () => clearTimeout(timer)
    }
  }, [step, timeLeft])

  // Formata√ß√£o do tempo
  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
  }

  const handlePixPayment = async () => {
    setStep('pix')
    
    try {
      // Gerar PIX via API
      const response = await fetch('/api/generate-pix', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ 
          amount: 0.50,
          description: 'An√°lise Completa - Discuss√µes de Relacionamento'
        })
      })
      
      const data = await response.json()
      
      if (data.success) {
        setPixCode(data.pixCode)
        setQrCodeUrl(data.qrCodeUrl)
        setPaymentId(data.paymentId)
      } else {
        alert('Erro ao gerar PIX. Tente novamente.')
        setStep('select')
      }
    } catch (error) {
      console.error('Erro ao gerar PIX:', error)
      alert('Erro ao gerar PIX. Tente novamente.')
      setStep('select')
    }
  }

  const checkPaymentStatus = async () => {
    if (!paymentId) return
    
    setStep('checking')
    
    try {
      const response = await fetch(`/api/check-payment/${paymentId}`)
      const data = await response.json()
      
      if (data.paid) {
        setStep('success')
        setTimeout(() => {
          onPaymentSuccess()
        }, 2000)
      } else {
        setStep('pix')
        alert('Pagamento ainda n√£o identificado. Verifique se o PIX foi realizado.')
      }
    } catch (error) {
      console.error('Erro ao verificar pagamento:', error)
      setStep('pix')
      alert('Erro ao verificar pagamento. Tente novamente.')
    }
  }

  const copyPixCode = () => {
    navigator.clipboard.writeText(pixCode)
    alert('C√≥digo PIX copiado!')
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-2xl shadow-2xl max-w-md w-full p-6 transform transition-all max-h-[90vh] overflow-y-auto">
        
        {/* Sele√ß√£o de Pagamento */}
        {step === 'select' && (
          <>
            <div className="text-center mb-6">
              <div className="text-4xl mb-3">üí≥</div>
              <h2 className="text-2xl font-bold text-gray-800 mb-2">
                Desbloquear Resposta Completa
              </h2>
              <p className="text-gray-600">
                Acesse a an√°lise completa da IA com sugest√µes detalhadas
              </p>
            </div>

            <div className="bg-gradient-to-r from-yellow-400 to-orange-500 text-white rounded-xl p-4 text-center mb-6">
              <div className="text-3xl font-bold">R$ 0,50</div>
              <div className="text-sm opacity-90">Pagamento √∫nico</div>
            </div>

            <div className="space-y-3 mb-6">
              <div className="flex items-center space-x-3">
                <div className="w-5 h-5 bg-green-500 rounded-full flex items-center justify-center">
                  <span className="text-white text-xs">‚úì</span>
                </div>
                <span className="text-gray-700">An√°lise completa e detalhada</span>
              </div>
              <div className="flex items-center space-x-3">
                <div className="w-5 h-5 bg-green-500 rounded-full flex items-center justify-center">
                  <span className="text-white text-xs">‚úì</span>
                </div>
                <span className="text-gray-700">Sugest√µes para melhorar a comunica√ß√£o</span>
              </div>
              <div className="flex items-center space-x-3">
                <div className="w-5 h-5 bg-green-500 rounded-full flex items-center justify-center">
                  <span className="text-white text-xs">‚úì</span>
                </div>
                <span className="text-gray-700">Dicas personalizadas para o casal</span>
              </div>
            </div>

            <div className="space-y-3">
              <button
                onClick={handlePixPayment}
                className="w-full bg-gradient-to-r from-green-500 to-blue-600 text-white font-semibold py-3 px-6 rounded-lg hover:from-green-600 hover:to-blue-700 transition-all duration-200 transform hover:scale-105 flex items-center justify-center space-x-2"
              >
                <span>üè¶</span>
                <span>Pagar com PIX</span>
              </button>
              
              <button
                onClick={onClose}
                className="w-full bg-gray-200 text-gray-700 font-semibold py-3 px-6 rounded-lg hover:bg-gray-300 transition-all duration-200"
              >
                Cancelar
              </button>
            </div>
          </>
        )}

        {/* Tela PIX */}
        {step === 'pix' && (
          <>
            <div className="text-center mb-6">
              <div className="text-4xl mb-3">üè¶</div>
              <h2 className="text-2xl font-bold text-gray-800 mb-2">
                Pagamento PIX
              </h2>
              <p className="text-gray-600">
                Escaneie o QR Code ou copie o c√≥digo PIX
              </p>
            </div>

            <div className="bg-red-50 border border-red-200 rounded-lg p-3 mb-4 text-center">
              <p className="text-red-600 font-medium">
                ‚è∞ Tempo restante: {formatTime(timeLeft)}
              </p>
            </div>

            {qrCodeUrl && (
              <div className="text-center mb-4">
                <img 
                  src={qrCodeUrl} 
                  alt="QR Code PIX" 
                  className="mx-auto w-48 h-48 border rounded-lg"
                />
              </div>
            )}

            <div className="bg-gray-50 rounded-lg p-4 mb-4">
              <p className="text-sm text-gray-600 mb-2">C√≥digo PIX:</p>
              <div className="bg-white border rounded p-2 text-xs font-mono break-all">
                {pixCode || 'Gerando c√≥digo PIX...'}
              </div>
              {pixCode && (
                <button
                  onClick={copyPixCode}
                  className="w-full mt-2 bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600 transition-all"
                >
                  üìã Copiar C√≥digo PIX
                </button>
              )}
            </div>

            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-4">
              <h3 className="font-semibold text-blue-800 mb-2">Como pagar:</h3>
              <ol className="text-sm text-blue-700 space-y-1">
                <li>1. Abra seu app do banco</li>
                <li>2. Escolha a op√ß√£o PIX</li>
                <li>3. Escaneie o QR Code ou cole o c√≥digo</li>
                <li>4. Confirme o pagamento de R$ 0,50</li>
                <li>5. Clique em "J√° Paguei" abaixo</li>
              </ol>
            </div>

            <div className="space-y-3">
              <button
                onClick={checkPaymentStatus}
                className="w-full bg-green-500 text-white font-semibold py-3 px-6 rounded-lg hover:bg-green-600 transition-all duration-200"
              >
                ‚úÖ J√° Paguei - Verificar Pagamento
              </button>
              
              <button
                onClick={() => setStep('select')}
                className="w-full bg-gray-200 text-gray-700 font-semibold py-3 px-6 rounded-lg hover:bg-gray-300 transition-all duration-200"
              >
                ‚Üê Voltar
              </button>
            </div>
          </>
        )}

        {/* Verificando Pagamento */}
        {step === 'checking' && (
          <div className="text-center">
            <div className="text-6xl mb-4">üîç</div>
            <h2 className="text-2xl font-bold text-blue-600 mb-2">
              Verificando Pagamento...
            </h2>
            <p className="text-gray-600 mb-4">
              Aguarde enquanto confirmamos seu pagamento
            </p>
            <div className="w-full bg-gray-200 rounded-full h-2 mb-4">
              <div className="bg-blue-500 h-2 rounded-full animate-pulse" style={{ width: '100%' }}></div>
            </div>
            <div className="flex items-center justify-center space-x-2">
              <div className="w-4 h-4 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
              <span className="text-gray-500">Verificando...</span>
            </div>
          </div>
        )}

        {/* Sucesso */}
        {step === 'success' && (
          <div className="text-center">
            <div className="text-6xl mb-4">‚úÖ</div>
            <h2 className="text-2xl font-bold text-green-600 mb-2">
              Pagamento Confirmado!
            </h2>
            <p className="text-gray-600 mb-4">
              Desbloqueando resposta completa...
            </p>
            <div className="w-full bg-gray-200 rounded-full h-2 mb-4">
              <div className="bg-green-500 h-2 rounded-full animate-pulse" style={{ width: '100%' }}></div>
            </div>
            <p className="text-sm text-gray-500">
              Redirecionando automaticamente...
            </p>
          </div>
        )}
      </div>
    </div>
  )
}
