'use client'

import { useState } from 'react'
import VoiceRecorder from '@/components/VoiceRecorder'
import PaymentModal from '@/components/PaymentModal'

type Step = 'home' | 'recording' | 'result'

export default function Home() {
  const [step, setStep] = useState<Step>('home')
  const [textoPessoaA, setTextoPessoaA] = useState('')
  const [textoPessoaB, setTextoPessoaB] = useState('')
  const [iaResponse, setIaResponse] = useState('')
  const [showPaymentModal, setShowPaymentModal] = useState(false)
  const [loading, setLoading] = useState(false)
  const [showFullResponse, setShowFullResponse] = useState(false)

  const handleStartDiscussion = () => {
    setStep('recording')
  }

  const handleSendForAnalysis = async () => {
    if (!textoPessoaA || !textoPessoaB) {
      alert('Por favor, grave ambos os relatos antes de enviar.')
      return
    }

    setLoading(true)
    try {
      const response = await fetch('/api/analyze', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ textoPessoaA, textoPessoaB }),
      })

      if (!response.ok) {
        throw new Error('Erro ao processar an√°lise')
      }

      const data = await response.json()
      setIaResponse(data.iaResponse)
      setStep('result')
    } catch (error) {
      alert('Erro ao processar an√°lise. Tente novamente.')
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  const handlePaymentSuccess = () => {
    setShowPaymentModal(false)
    setShowFullResponse(true)
  }

  const handleRestart = () => {
    setStep('home')
    setTextoPessoaA('')
    setTextoPessoaB('')
    setIaResponse('')
    setShowFullResponse(false)
    setShowPaymentModal(false)
  }

  const getPreviewText = (text: string) => {
    const lines = text.split('\n')
    return lines.slice(0, 3).join('\n')
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 to-purple-50 p-4">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-800 mb-2">
            üíï Discuss√µes de Relacionamento
          </h1>
          <p className="text-gray-600">
            Descubra quem est√° certo na discuss√£o com an√°lise imparcial por IA
          </p>
        </div>

        {/* Home Screen */}
        {step === 'home' && (
          <div className="text-center space-y-8">
            <div className="bg-white rounded-2xl shadow-lg p-8 max-w-md mx-auto">
              <div className="text-6xl mb-6">ü§ù</div>
              <h2 className="text-2xl font-semibold text-gray-800 mb-4">
                Bem-vindos!
              </h2>
              <p className="text-gray-600 mb-6">
                Gravem seus pontos de vista sobre a discuss√£o e deixem nossa IA analisar quem tem mais raz√£o.
              </p>
              <button
                onClick={handleStartDiscussion}
                className="w-full bg-gradient-to-r from-pink-500 to-purple-600 text-white font-semibold py-3 px-6 rounded-lg hover:from-pink-600 hover:to-purple-700 transition-all duration-200 transform hover:scale-105"
              >
                Come√ßar Discuss√£o
              </button>
            </div>
            
            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 max-w-2xl mx-auto">
              <p className="text-yellow-800 text-sm">
                ‚ö†Ô∏è Este site √© para fins de entretenimento e reflex√£o. N√£o substitui terapia profissional.
              </p>
            </div>
          </div>
        )}

        {/* Recording Screen */}
        {step === 'recording' && (
          <div className="space-y-6">
            <div className="text-center">
              <h2 className="text-2xl font-semibold text-gray-800 mb-2">
                Gravem seus relatos
              </h2>
              <p className="text-gray-600">
                Cada pessoa deve gravar sua vers√£o da discuss√£o
              </p>
            </div>

            <div className="grid md:grid-cols-2 gap-6">
              {/* Pessoa A */}
              <div className="bg-white rounded-xl shadow-lg p-6">
                <div className="text-center mb-4">
                  <div className="text-4xl mb-2">üë®</div>
                  <h3 className="text-xl font-semibold text-blue-600">
                    Pessoa A (Homem)
                  </h3>
                </div>
                <VoiceRecorder
                  onTextChange={setTextoPessoaA}
                  placeholder="O relato da Pessoa A aparecer√° aqui..."
                />
              </div>

              {/* Pessoa B */}
              <div className="bg-white rounded-xl shadow-lg p-6">
                <div className="text-center mb-4">
                  <div className="text-4xl mb-2">üë©</div>
                  <h3 className="text-xl font-semibold text-pink-600">
                    Pessoa B (Mulher)
                  </h3>
                </div>
                <VoiceRecorder
                  onTextChange={setTextoPessoaB}
                  placeholder="O relato da Pessoa B aparecer√° aqui..."
                />
              </div>
            </div>

            <div className="text-center space-y-4">
              <button
                onClick={handleSendForAnalysis}
                disabled={!textoPessoaA || !textoPessoaB || loading}
                className="bg-gradient-to-r from-green-500 to-blue-600 text-white font-semibold py-3 px-8 rounded-lg hover:from-green-600 hover:to-blue-700 transition-all duration-200 transform hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none"
              >
                {loading ? 'Analisando...' : 'Enviar para Avalia√ß√£o'}
              </button>
              
              <button
                onClick={handleRestart}
                className="block mx-auto text-gray-500 hover:text-gray-700 underline"
              >
                Reiniciar Debate
              </button>
            </div>
          </div>
        )}

        {/* Result Screen */}
        {step === 'result' && (
          <div className="space-y-6">
            <div className="text-center">
              <h2 className="text-2xl font-semibold text-gray-800 mb-2">
                An√°lise da IA
              </h2>
              <p className="text-gray-600">
                Resultado imparcial baseado nos relatos gravados
              </p>
            </div>

            <div className="bg-white rounded-xl shadow-lg p-6">
              <div className="text-center mb-6">
                <div className="text-4xl mb-2">ü§ñ</div>
                <h3 className="text-xl font-semibold text-purple-600">
                  Veredicto da IA
                </h3>
              </div>

              <div className="bg-gray-50 rounded-lg p-4 mb-4">
                <pre className="whitespace-pre-wrap text-gray-700 font-medium">
                  {showFullResponse ? iaResponse : getPreviewText(iaResponse)}
                </pre>
                
                {!showFullResponse && iaResponse && (
                  <div className="mt-4 text-center">
                    <div className="bg-gradient-to-r from-yellow-400 to-orange-500 text-white p-3 rounded-lg mb-4">
                      <p className="font-semibold">
                        üîí Resposta completa bloqueada
                      </p>
                      <p className="text-sm">
                        Desbloqueie a an√°lise completa com sugest√µes detalhadas
                      </p>
                    </div>
                    <button
                      onClick={() => setShowPaymentModal(true)}
                      className="bg-gradient-to-r from-yellow-500 to-orange-600 text-white font-semibold py-3 px-6 rounded-lg hover:from-yellow-600 hover:to-orange-700 transition-all duration-200 transform hover:scale-105"
                    >
                      Desbloquear Resposta Completa por R$ 0,50
                    </button>
                  </div>
                )}
              </div>
            </div>

            <div className="text-center">
              <button
                onClick={handleRestart}
                className="bg-gray-500 text-white font-semibold py-3 px-6 rounded-lg hover:bg-gray-600 transition-all duration-200"
              >
                Reiniciar Debate
              </button>
            </div>
          </div>
        )}

        {/* Payment Modal */}
        {showPaymentModal && (
          <PaymentModal
            onClose={() => setShowPaymentModal(false)}
            onPaymentSuccess={handlePaymentSuccess}
          />
        )}
      </div>
    </div>
  )
}
