'use client'

import { useState, useEffect, useRef } from 'react'

// Declara√ß√µes para TypeScript
declare global {
  interface Window {
    SpeechRecognition: any
    webkitSpeechRecognition: any
  }
}

interface VoiceRecorderProps {
  onTextChange: (text: string) => void
  placeholder: string
}

export default function VoiceRecorder({ onTextChange, placeholder }: VoiceRecorderProps) {
  const [isRecording, setIsRecording] = useState(false)
  const [text, setText] = useState('')
  const [isSupported, setIsSupported] = useState(true)
  const [mode, setMode] = useState<'voice' | 'text'>('voice')
  const [interimText, setInterimText] = useState('')
  const recognitionRef = useRef<any>(null)

  useEffect(() => {
    // Verificar se o navegador suporta reconhecimento de voz
    if (typeof window !== 'undefined') {
      const SpeechRecognition = window.SpeechRecognition || (window as any).webkitSpeechRecognition
      
      if (!SpeechRecognition) {
        setIsSupported(false)
        setMode('text') // For√ßar modo texto se n√£o suportar voz
        return
      }

      const recognition = new SpeechRecognition()
      recognition.continuous = true
      recognition.interimResults = true
      recognition.lang = 'pt-BR'

      recognition.onstart = () => {
        console.log('Reconhecimento de voz iniciado')
      }

      recognition.onresult = (event: any) => {
        let finalTranscript = ''
        let interim = ''

        for (let i = event.resultIndex; i < event.results.length; i++) {
          const transcript = event.results[i][0].transcript
          if (event.results[i].isFinal) {
            finalTranscript += transcript + ' '
          } else {
            interim += transcript
          }
        }

        if (finalTranscript) {
          const newText = text + finalTranscript
          setText(newText)
          onTextChange(newText)
        }
        
        setInterimText(interim)
      }

      recognition.onerror = (event: any) => {
        console.error('Erro no reconhecimento de voz:', event.error)
        setIsRecording(false)
        setInterimText('')
        
        let errorMessage = 'Erro no reconhecimento de voz. '
        switch (event.error) {
          case 'not-allowed':
            errorMessage += 'Permiss√£o para usar o microfone foi negada. Permita o acesso ao microfone e tente novamente.'
            break
          case 'no-speech':
            errorMessage += 'Nenhuma fala detectada. Tente falar mais alto ou verificar o microfone.'
            break
          case 'audio-capture':
            errorMessage += 'Erro ao capturar √°udio. Verifique se o microfone est√° funcionando.'
            break
          case 'network':
            errorMessage += 'Erro de rede. Verifique sua conex√£o com a internet.'
            break
          default:
            errorMessage += 'Tente usar o modo de digita√ß√£o como alternativa.'
        }
        
        alert(errorMessage)
      }

      recognition.onend = () => {
        setIsRecording(false)
        setInterimText('')
        console.log('Reconhecimento de voz finalizado')
      }

      recognitionRef.current = recognition
    }

    return () => {
      if (recognitionRef.current) {
        recognitionRef.current.stop()
      }
    }
  }, [text, onTextChange])

  const startRecording = () => {
    if (recognitionRef.current && !isRecording) {
      try {
        setIsRecording(true)
        setInterimText('')
        recognitionRef.current.start()
      } catch (error) {
        console.error('Erro ao iniciar grava√ß√£o:', error)
        setIsRecording(false)
        alert('Erro ao iniciar grava√ß√£o. Tente usar o modo de digita√ß√£o.')
      }
    }
  }

  const stopRecording = () => {
    if (recognitionRef.current && isRecording) {
      recognitionRef.current.stop()
      setIsRecording(false)
      setInterimText('')
    }
  }

  const handleTextChange = (newText: string) => {
    setText(newText)
    onTextChange(newText)
  }

  const clearText = () => {
    setText('')
    setInterimText('')
    onTextChange('')
  }

  return (
    <div className="space-y-4">
      {/* Seletor de Modo */}
      <div className="flex bg-gray-100 rounded-lg p-1">
        <button
          onClick={() => setMode('voice')}
          disabled={!isSupported}
          className={`flex-1 py-2 px-4 rounded-md text-sm font-medium transition-all ${
            mode === 'voice' && isSupported
              ? 'bg-white text-purple-600 shadow-sm'
              : 'text-gray-500 hover:text-gray-700'
          } ${!isSupported ? 'opacity-50 cursor-not-allowed' : ''}`}
        >
          üé§ Grava√ß√£o de Voz
        </button>
        <button
          onClick={() => setMode('text')}
          className={`flex-1 py-2 px-4 rounded-md text-sm font-medium transition-all ${
            mode === 'text'
              ? 'bg-white text-purple-600 shadow-sm'
              : 'text-gray-500 hover:text-gray-700'
          }`}
        >
          ‚úèÔ∏è Digita√ß√£o
        </button>
      </div>

      {/* Modo Grava√ß√£o de Voz */}
      {mode === 'voice' && isSupported && (
        <>
          {/* Bot√£o de Grava√ß√£o */}
          <div className="text-center">
            <button
              onClick={isRecording ? stopRecording : startRecording}
              className={`w-20 h-20 rounded-full font-semibold text-white transition-all duration-200 transform hover:scale-105 ${
                isRecording
                  ? 'bg-red-500 hover:bg-red-600 animate-pulse'
                  : 'bg-green-500 hover:bg-green-600'
              }`}
            >
              {isRecording ? '‚èπÔ∏è' : 'üé§'}
            </button>
            <p className="text-sm text-gray-600 mt-2">
              {isRecording ? 'Gravando... Clique para parar' : 'Clique para gravar'}
            </p>
          </div>

          {/* Status de Grava√ß√£o */}
          {isRecording && (
            <div className="bg-red-50 border border-red-200 rounded-lg p-3 text-center">
              <div className="flex items-center justify-center space-x-2">
                <div className="w-3 h-3 bg-red-500 rounded-full animate-pulse"></div>
                <span className="text-red-600 font-medium">Gravando... Fale agora!</span>
              </div>
            </div>
          )}

          {/* √Årea de Texto com Preview */}
          <div className="bg-gray-50 border border-gray-200 rounded-lg p-4 min-h-[150px]">
            <div className="flex justify-between items-center mb-2">
              <span className="text-sm font-medium text-gray-700">Texto reconhecido:</span>
              {text && (
                <button
                  onClick={clearText}
                  className="text-xs text-red-500 hover:text-red-700 underline"
                >
                  Limpar
                </button>
              )}
            </div>
            <div className="min-h-[100px] text-gray-700">
              {text && <span>{text}</span>}
              {interimText && <span className="text-gray-400 italic">{interimText}</span>}
              {!text && !interimText && (
                <span className="text-gray-400 italic">{placeholder}</span>
              )}
            </div>
          </div>

          {/* Instru√ß√µes para Grava√ß√£o */}
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-3">
            <p className="text-blue-700 text-sm">
              üí° <strong>Dicas para melhor grava√ß√£o:</strong><br />
              ‚Ä¢ Fale de forma clara e pausada<br />
              ‚Ä¢ Mantenha o microfone pr√≥ximo<br />
              ‚Ä¢ Evite ru√≠dos de fundo<br />
              ‚Ä¢ Se houver problemas, use o modo de digita√ß√£o
            </p>
          </div>
        </>
      )}

      {/* Modo Digita√ß√£o */}
      {mode === 'text' && (
        <>
          <div className="space-y-3">
            <div className="flex justify-between items-center">
              <label className="text-sm font-medium text-gray-700">
                Digite seu relato:
              </label>
              {text && (
                <button
                  onClick={clearText}
                  className="text-xs text-red-500 hover:text-red-700 underline"
                >
                  Limpar
                </button>
              )}
            </div>
            
            <textarea
              value={text}
              onChange={(e) => handleTextChange(e.target.value)}
              placeholder={placeholder}
              className="w-full p-4 border border-gray-300 rounded-lg resize-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all"
              rows={8}
            />
            
            <div className="text-right">
              <span className="text-xs text-gray-500">
                {text.length} caracteres
              </span>
            </div>
          </div>

          {/* Instru√ß√µes para Digita√ß√£o */}
          <div className="bg-green-50 border border-green-200 rounded-lg p-3">
            <p className="text-green-700 text-sm">
              ‚úèÔ∏è <strong>Dicas para um bom relato:</strong><br />
              ‚Ä¢ Seja espec√≠fico sobre a situa√ß√£o<br />
              ‚Ä¢ Descreva seus sentimentos<br />
              ‚Ä¢ Explique seu ponto de vista<br />
              ‚Ä¢ Mantenha um tom respeitoso
            </p>
          </div>
        </>
      )}

      {/* Aviso quando voz n√£o √© suportada */}
      {!isSupported && (
        <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-3">
          <p className="text-yellow-700 text-sm">
            ‚ö†Ô∏è <strong>Reconhecimento de voz n√£o dispon√≠vel.</strong><br />
            Use o modo de digita√ß√£o ou tente um navegador compat√≠vel (Chrome, Edge, Safari).
          </p>
        </div>
      )}
    </div>
  )
}
